.sect_slider {
	margin-bottom: rem(14);
	
	.swiper-container {
		border-radius: rem(10);

		.swiper-pagination {
			position: absolute;
			width: auto;
			bottom: rem(40);
			left: rem(125);

			&-bullet {
				opacity: 1;
				width: rem(68) !important;
				border-radius: rem(2) !important;
				height: rem(2) !important;
				background-color: rgba(255,255,255,.5) !important;

				margin: 0 rem(10) 0 0 !important;

				&:last-child {
					margin: 0;
				}
			}

			&-bullet-active {
				background-color: white !important;
			}
		}
		
		.swiper-nav-prev,
		.swiper-nav-next {
			position: absolute;
			top: 50%;
			z-index: 10;
			margin-top: rem(-9);
		}
		.swiper-nav-prev {
			left: rem(18);
		}
		.swiper-nav-next {
			right: rem(18);
		}
	}

	&__item {
		position: relative;
		height: rem(396);
		padding: rem(82) rem(125);
		border-radius: rem(10);
		background: linear-gradient(89.57deg, rgba(0, 0, 0, 0.63) 11.27%, rgba(125, 125, 125, 0.24) 99.68%);

		&-image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			z-index: -1;
			// height: 100%;
			// object-fit: cover;
		}

		.title-32 {
			color: white;
		}

		.text, .button-gray {
			margin-top: rem(14);
		}

		.text {
			max-width: rem(380);

			font-weight: 500;
			line-height: 140%;
			color: white;
		}

		.button-gray {

		}
	}

}

.swiper-nav-wrap {
	display: flex;
	flex-wrap: wrap;

	.swiper-nav-prev {
		margin-right: rem(11);
	}
}

.swiper-nav-prev,
.swiper-nav-next {
	width: rem(44);
	height: rem(44);
	border-radius: 50%;

	border: solid rem(0.5) rgba(0, 0, 0, 0.1);
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25),
	0 4px 25px 0 rgba(0, 0, 0, 0.25);
	background-color: #ffffff;

	display: flex;
	align-items: center;
	justify-content: center;

	i {
		font-size: rem(11);
	}
}
.swiper-nav-next {
	i {
		margin-left: rem(3);
	}
}
.swiper-button-disabled {
	i {
		&::before {
			color: #595959;
			opacity: .3;
		}
	}
}

.swiper-pagination {
	display: flex;
	flex-wrap: wrap;

	&-bullet {
		opacity: 1 !important;
		width: rem(37) !important;
		height: rem(2) !important;
		border-radius: rem(2) !important;
		background-color: var(--c-black-50) !important;

		margin-right: rem(12);

		&:last-child {
			margin-right: 0;
		}
	}

	&-bullet-active {
		background-color: black !important;
	}
}