.cat_items {
	display: flex;
	flex-wrap: wrap;

	margin-bottom: rem(88);

	&__block {
		width: calc((100% - 3rem) / 3);
		margin-right: rem(24);
		margin-bottom: rem(24);

		border-radius: rem(5);
		overflow: hidden;
		border: rem(0.5) solid rgba(0, 0, 0, 0.1);
		
		transition: all 250ms ease;

		&:hover {
			box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
			0px 4px 4px rgba(0, 0, 0, 0.25);

			.text p {
				color: var(--c-red);
			}
		}

		&:nth-child(3n) {
			margin-right: 0;
		}

		img {
			width: 100%;
			height: rem(203);
			object-fit: cover;
		}

		.text {
			padding: rem(16) rem(24);
			background-color: white;

			p {
				font-size: rem(16);
				font-weight: 500;
				color: #424242;
			}
		}
	}
}