.section-message {
	display: flex;
	justify-content: center;
}

.message_form {
	border-radius: rem(5);
	width: rem(483);
	background-color: white;
	padding: rem(18) rem(24);

	border: rem(0.5) solid rgba(0, 0, 0, 0.2);
	box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
	0px 4px 4px rgba(0, 0, 0, 0.25);

	&__header {
		margin-bottom: rem(18);

		display: flex;
		flex-direction: column;

		.title-24 {
			font-weight: bold;
		}

		p {
			margin-top: rem(18);
			font-size: rem(12);
			font-weight: 500;
			line-height: 165%;
			color: var(--c-black-50);
		}
	}

	&__content {
		.field-attach {
			margin-bottom: rem(18);
		}
		
		.field-checked {
			margin-bottom: rem(24);
			.check {
				position: relative;
				width: rem(13) !important;
				height: rem(13) !important;

				&::before {
					position: absolute;
					top: rem(-2);
					left: 0;
					font-size: rem(12) !important;
				}
			}
		}

		.button-red-border {
			width: 100%;
			padding: rem(23) 0;

			span {
				font-size: rem(14);
				font-weight: 500;
			}
		}
	}
}