.section-popular {
	margin-bottom: rem(90);

	.item_button_next {
		font-size: rem(12);
		font-weight: 500;
		color: var(--c-black-50);
		line-height: 119%;

		border-bottom: rem(0.5) dotted var(--c-black-50);

		&:hover {
			color: black;
			border-color: black;
		}
	}
}

.tab_wrap {
	display: flex;
	flex-direction: column;

	&__nav {
		margin-bottom: rem(24);

		ul {
			display: flex;
			flex-wrap: wrap;

			border-bottom: rem(2) solid #E1E1E1;

			li {
				cursor: pointer;
				margin-right: rem(40);

				&:last-child {
					margin-right: 0;
				}

				a {
					pointer-events: none;
					position: relative;
					font-weight: 500;
					color: var(--c-black-50);
					padding: rem(14) 0;
					display: block;

					&::after {
						content: "";
						position: absolute;
						bottom: rem(-2);
						left: 0;
						width: 100%;
						height: rem(2);
						border-radius: rem(1);
						background-color: var(--c-red);
						opacity: 0;
						transition: all 250ms ease;
					}

					&:hover {
						color: black;
						&::after {
							opacity: 1;
						}
					}
				}
			}

			li.active {
				a {
					color: black;

					&::after {
						opacity: 1;
					}
				}
			}
		}
	}

	&__tabs {
		.tab_content {
			display: none;
		}
		.tab_content.active {
			display: block;
		}
	}
}

.group-items-3 {
	display: flex;
	flex-wrap: wrap;

	margin-bottom: rem(70);

	.item_product {
		width: calc((100% - 3rem) / 3);
		margin-right: rem(24);

		&:nth-child(3n) {
			margin-right: 0;
		}
	}
}

.item_product {
	position: relative;
	display: flex;
	flex-direction: column;

	transition: all 250ms ease;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: white;
		border-radius: rem(5);

		transition: all 250ms ease;
		// box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
		// 0px 4px 4px rgba(0, 0, 0, 0.25);
		z-index: -1;
	}

	&:hover {

		&::before {
			width: calc(100% + 1.250rem);
			height: calc(100% + 1.250rem);
			margin: rem(-10);
			padding: rem(10);
			box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
			0px 4px 4px rgba(0, 0, 0, 0.25);
		}

		.item_product__top_favorite {
			opacity: 1;
			pointer-events: visible;
		}

		.button-gray-border {
			border-color: var(--c-red);
			background-color: var(--c-red);

			span {
				color: white;
			}
		}

		.button-default {
			color: black;
			border-color: black;
		}
	}

	&__top {
		position: relative;
		height: rem(180);
		margin-bottom: rem(7);

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&_favorite {
			position: absolute;
			top: rem(11);
			right: rem(9);
			z-index: 10;

			transition: all 250ms ease;

			opacity: 0;
			pointer-events: none;

			&:hover {
				i {
					&::before {
						color: var(--c-red);
					}
				}
			}

			i {
				font-size: rem(18);
			}
		}
	}

	&__bottom {
		display: flex;
		flex-direction: column;

		&_first {
			position: relative;
			color: var(--c-gray);
			font-size: rem(10);
			line-height: 119%;
			margin-bottom: rem(2);
			
			align-self: baseline;

			&::after {
				content: "";
				position: absolute;
				top: 50%;
				margin-top: rem(-1);
				left: 0;
				height: rem(1);
				width: 100%;
				background-color: var(--c-gray);
			}
		}

		&_price-text {
			font-size: rem(12);
			line-height: 119%;
			font-weight: 500;
			color: var(--c-gray);
			margin-bottom: rem(25);
		}

		&_price {
			font-size: rem(18);
			font-weight: bold;
			line-height: 119%;
			margin-bottom: rem(10);
		}

		&_list {
			display: flex;
			flex-direction: column;
			margin-bottom: rem(30);

			li {
				display: flex;
				flex-wrap: wrap;
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}

				b, span {
					font-size: rem(10);
					line-height: 119%;
				}

				b {
					flex: 0 0 rem(112);
					max-width: rem(112);
				}

				span {
					flex: 1;
				}
			}
		}

		&-buttons {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.button-gray-border {
				margin-right: rem(23);
			}

			.button-default {
				font-size: rem(10);
				line-height: 148%;
				color: var(--c-black-50);
				border-bottom: rem(1) solid var(--c-black-50);
			}
		}
	}
}