.wg-favorite {
	position: fixed;
	top: 50%;
	margin-top: rem(-23);
	right: rem(-31);

	width: rem(88);
	height: rem(47);

	transition: all 250ms ease;

	display: flex;
	align-items: center;
	
	border-top-left-radius: rem(5);
	border-bottom-left-radius: rem(5);

	background-color: white;

	box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
	0px 4px 4px rgba(0, 0, 0, 0.25);

	z-index: 20;

	padding-left: rem(14);

	&:hover {
		right: 0;
	}

	i {
		font-size: rem(18);
		margin-right: rem(5);
		&::before {
			color: var(--c-red);
		}
	}

	span {
		font-size: rem(12);
	}
}