.footer {
	position: relative;
	margin-top: rem(75);

	&__arrow-top {
		cursor: pointer;
		position: absolute;
		top: 50%;
		margin-top: rem(-25);
		left: rem(10);

		width: rem(50);
		height: rem(50);
		border-radius: 50%;
		box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.1),
		0px 0px 10px 0px rgba(174, 174, 174, 0.07);

		display: flex;
		align-items: center;
		justify-content: center;

		transition: all 250ms ease;

		&:hover {
			box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.4),
			0px 0px 10px 0px rgba(174, 174, 174, 0.25);

		}

		i {
			font-size: rem(23);
		}
	}

	&__top {
		border-top: rem(0.5) solid #D2D2D2;
		

		.container {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		// &-left, &-right {
		// 	flex-grow: 1;
		// 	flex-shrink: 0;
		// }

		&-left {
			display: flex;
			flex-direction: column;
			padding-top: rem(26);
			padding-bottom: rem(52);
			padding-right: rem(35);
			border-right: rem(0.5) solid #D2D2D2;

			flex: 0 0 rem(466);
			max-width: rem(466);

			.logo {
				margin-bottom: rem(25);
			}

			.time {
				margin-bottom: rem(30);
				font-weight: 600;
			}

			.mail {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(34);

				&:hover {
					color: var(--c-red);
				}

				i {
					margin-right: rem(7);
				}
			}

			.links {
				display: flex;
				// flex-wrap: wrap;
				margin-bottom: rem(50);

				&:last-of-type {
					margin-bottom: 0;
				}

				ul {
					display: flex;
					flex-direction: column;

					li {

						margin-bottom: rem(9);

						&:last-child {
							margin-bottom: 0;
							align-self: baseline;

							a {
								font-size: rem(10);
								font-weight: 500;
								color: var(--c-black-50);
								border-bottom: rem(1) solid var(--c-black-50);

								&:hover {
									color: black;
									border-color: black;
								}
							}
						}

						a {
							&:hover {
								color: var(--c-red);
							}
						}
					}
				}

				.social {
					a {
						margin-right: rem(5);

						&:hover {
							opacity: .8;
						}

						&:last-child {
							margin-right: 0;
						}
					}
				}

				.pay {
					span {
						margin-bottom: rem(1);
						font-size: rem(12);
						font-weight: 500;
						color: var(--c-black-50);
					}

					&__images {

						display: flex;
						flex-wrap: wrap;
						align-items: center;

						img {
							margin-right: rem(14);

							&:last-child {
								margin-right: 0;
							}
						}
					}
				}


				&__left {
					flex: 0 0 rem(200);
					max-width: rem(200);
				}

				&__right {
					flex: 0 0 rem(240);
					max-width: rem(240);
				}
			}
		}

		&-right {
			display: flex;
			flex-wrap: wrap;
			padding-top: rem(64);
			// padding-left: rem(146);

			ul {
				margin-right: rem(90);

				&:last-child {
					margin-right: 0;
				}

				li {
					font-size: rem(16);
					line-height: 160%;

					color: var(--c-black-60);

					&:first-child {
						font-weight: bold;
						color: black;
					}

					a {
						&:hover {
							color: var(--c-red);
						}
					}
				}
			}
		}
	}

	&__bottom {
		border-top: rem(0.5) solid #D2D2D2;

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			padding-top: rem(19);
			padding-bottom: rem(24);
		}

		p, a, span  {
			font-size: rem(12);
			color: var(--c-black-40);

			transition: all 250ms ease;
		}

		a {
			&:hover {
				color: black;
			}
		}

		.logo {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			&:hover {
				span {
					color: black;
				}
			}

			span {
				margin-right: rem(7);
			}
		}
	}
}