.aside {
	flex: 0 0 rem(282);
	max-width: rem(282);

	margin-right: rem(24);

	.icon-furniture {
		font-size: rem(24) !important;
		margin-left: rem(-3);
	}

	> .title {
		margin-bottom: rem(30);
		
		font-weight: bold;
	}

	&__item {
		border-bottom: rem(0.5) solid rgba(0, 0, 0, .12);
		&:first-child {
			.aside__item-header {
				padding-top: 0;
			}
		}

		&-header {
			cursor: pointer;
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: center;

			padding: rem(32) 0;
			// border-top: rem(0.5) solid rgba(0, 0, 0, .12);
			// border-bottom: rem(0.5) solid rgba(0,0,0,.12);

			.text {
				pointer-events: none;
				display: flex;
				align-items: center;
				.image {
					width: rem(38);
					text-align: left;
					// display: flex;
					// justify-content: flex-start;
				}
				i {
					font-size: rem(19);
					margin-right: rem(16);
				}

				b {

				}
			}

			.icon-arrow-bottom {
				pointer-events: none;
				font-size: rem(6);

				&::before {
					color: #C4C4C4;
				}
			}
		}
		
		&-header.active {
			.icon-arrow-bottom {
				transform: rotate(180deg);
				&::before {
					color: var(--c-red);
					
				}
			}
		}

		&-content {
			padding-left: rem(38);
			padding-bottom: rem(25);

			display: none;

			

			.item {
				margin-bottom: rem(13);

				.field-checked.type-circle {
					&:first-child {
						padding-top: rem(10);
					}
				}

				&:last-child {
					margin-bottom: 0;
				}

				&:first-child {
					.title {
						padding-top: 0;
					}
				}

				.title {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: space-between;

					margin-bottom: rem(10);

					cursor: pointer;

					padding-top: rem(14);

					b {
						pointer-events: none;
						font-size: rem(12);
					}

					.icon-arrow-bottom {
						pointer-events: none;
						font-size: rem(6);

						&::before {
							color: #C4C4C4;
						}
					}
				}

				.title.active {
					.icon-arrow-bottom {
						transform: rotate(-180deg);

						&::before {
							color: var(--c-red);
						}
					}
				}

				.content {
					display: none;
				}
			}

			
		}
	}
}