.header {
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25),
	0 4px 25px 0 rgba(0, 0, 0, 0.25);
	margin-bottom: rem(32);

	&__top {
		background-color: #f3f3f3;
		height: rem(40);
		padding: rem(10) 0 rem(13) 0;

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
		}

		&-change {
			i {
				font-size: rem(16);
				margin-right: rem(2.5);
			}

			.text {
				font-weight: 500;
			}
		}

		&-text {
			margin-right: auto;
			margin-left: rem(59);
			p {
				font-weight: 500;
				font-size: rem(12);
			}
		}

		&-nav {
			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(38);

					&:last-child {
						margin-right: 0;
					}

					a {
						color: var(--c-black-50);

						&:hover {
							color: black;
						}
					}
				}
			}
		}
	}

	&__center {
		padding-top: rem(9);
		padding-bottom: rem(28.5);

		.container {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: flex-end;
		}

		&-logo {
			margin-right: rem(43);
			height: rem(59.5);

			a {
				display: block;
			}
		}

		&-search {
			width: 100%;
			max-width: rem(373);
			margin-right: rem(67);

			.form_search {
				position: relative;

				&-field {
					width: 100%;
					height: rem(43);
					border-radius: rem(2);
					border: solid rem(0.5) var(--c-black-10);
					padding: rem(13) rem(38) rem(13) rem(23);
					background-color: white;
					color: var(--c-gray-field);

					@include placeholder {
						color: var(--c-gray-field);
					}

					&:focus {
						border-color: black;
					}
				}

				&-button {
					position: absolute;
					top: 50%;
					right: rem(11);

					margin-top: rem(-11);

					&:hover {
						opacity: .8;
					}

					&:active {
						opacity: 1;
					}

					i {
						font-size: rem(15);
					}
				}
			}
		}

		&-contact {
			margin-right: rem(65);
			padding-bottom: rem(5);

			ul {
				display: flex;
				flex-direction: column;

				li {
					margin-bottom: rem(7);
					color: var(--c-black-60);
					font-size: rem(12);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						display: flex;
						align-items: center;

						&:hover {
							color: black;

							i::before {
								color: black;
							}
						}

						i {
							margin-right: rem(7);
							font-size: rem(10);
							margin-top: rem(2);
							
							&::before {
								color: var(--c-black-60);
							}
						}
					}
				}
			}
		}

		&-phone {
			margin-right: rem(20);

			ul {
				display: flex;
				flex-direction: column;

				li {
					margin-bottom: rem(8);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-size: rem(16);
						font-weight: 600;

						&:hover {
							color: var(--c-red);
						}
					}
				}
			}
		}

		&-button {
			.button-red-border {
				padding: rem(14.5) rem(19);
			}
		}
	}

	&__nav {
		

		border-top: rem(0.5) solid #D2D2D2;

		ul {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			padding-top: rem(5);
			padding-bottom: rem(5);

			> li {
				margin-right: rem(32);

				&:last-child {
					margin-right: 0;
					a {
						padding-right: 0;
					}
				}

				&:first-child {
					margin-right: auto;
					a {
						padding-left: 0;
					}
				}

				> a {
					font-size: rem(16);
					font-weight: 500;

					padding: rem(15) rem(16);
					border-radius: rem(5);
					
					display: flex;
					align-items: center;

					&:hover {
						color: var(--c-red);
					}

					.burger {
						margin-right: rem(8);
					}
					
					i {
						font-size: rem(6);
						margin-left: rem(4);
					}
				}

				
			}
			.drop-menu {
				position: relative;

				

				&-content {
					position: absolute;
					left: 0;
					top: 100%;
					z-index: 10;

					margin-top: rem(9);

					width: 100%;
					background-color: white;
					box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
					0px 4px 4px rgba(0, 0, 0, 0.25);
					border-radius: rem(5);

					// display: flex;
					display: none;
					flex-direction: column;
					overflow: hidden;

					padding: 0;

					li {
						width: 100%;
						transition: all 250ms ease;
						margin: 0;
						&:hover {
							background-color: rgba(196,196,196,.2);

							a {
								color: black;
							}
						}
						a {
							padding: rem(15) rem(20);
							color: var(--c-black-50);
							font-size: rem(14);
							white-space: nowrap;
							box-shadow: none;
						}
					}
				}
			}

			.drop-menu.active {
				> a {
					box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
						0px 4px 4px rgba(0, 0, 0, 0.25);
					color: var(--c-red);
					i {
						transform: rotate(180deg);
					}
				}

				.drop-menu-content {
					display: flex;
				}
			}
		}

		.burger {
			width: rem(12);
			height: rem(10);

			display: flex;
			flex-direction: column;
			justify-content: space-between;

			.line {
				width: 100%;
				height: rem(2);
				border-radius: rem(2);
				background-color: var(--c-red);
			}
		}
	}
}