.section-questions {
	margin-bottom: rem(54);
}

.accordion {
	display: flex;
	flex-direction: column;

	&__item {
		border-top: rem(1) dashed var(--c-black-20);
		border-bottom: rem(1) dashed var(--c-black-20);
		&-header {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			padding-top: rem(24);
			padding-bottom: rem(32);

			cursor: pointer;

			.title {
				pointer-events: none;
				font-size: rem(16);
				font-weight: 600;
				line-height: 112%;
			}

			.control {
				pointer-events: none;
				position: relative;
				width: rem(23);
				height: rem(23);

				.line {
					position: absolute;
					// width: 100%;
					// height: rem(1.57);
					background-color: var(--c-red);
					border-radius: rem(2);
					display: block;

					transition: all 250ms ease;

					&:first-child {
						top: 50%;
						left: 0;
						width: 100%;
						height: rem(2);
						margin-top: rem(-1);
					}

					&:last-child {
						top: 0;
						left: 50%;
						// margin-left: rem(-1);
						width: rem(1.57);
						height: 100%;
					}
				}
			}
		}

		&-text {
			padding-bottom: rem(14);

			display: none;

			p {
				font-size: rem(16);
				line-height: 140%;
				color: var(--c-black-70);
			}
		}
	}

	&__item.active {

		.accordion__item-header {
			.control {
				.line {
					&:first-child {
						background-color: black;
					}

					&:last-child {
						left: 100%;
						opacity: 0;
					}
				}
			}
		}

		.accordion__item-text {
			display: block;
		}
	}
}