.section-about {
	height: rem(508);
	margin-bottom: rem(104);
	position: relative;
	background: linear-gradient(89.57deg, rgba(0, 0, 0, 0.63) 0.41%, rgba(46, 40, 40, 0.74) 99.68%);
	padding: rem(35) rem(86) 0 rem(96);

	&__image {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__content {
		
		border-radius: rem(10);
		background: rgba(255, 255, 255, 0.2);
		backdrop-filter: blur(10px);

		padding: rem(36) rem(48);

		img {
			margin-bottom: rem(73);
		}

		.title-24 {
			margin-bottom: rem(10);
			color: white;
		}

		p {
			line-height: 140%;
			color: white;

			margin-bottom: rem(28);

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}