.button-red-border {
	border-radius: rem(5);
	border: rem(1) solid var(--c-red);
	display: inline-block;

	&:hover {
		background-color: var(--c-red);

		span {
			color: white;
		}
	}

	span {
		color: var(--c-red);
		font-size: rem(12);
	}
}

.button-gray-border {
	border-radius: rem(1);
	background-color: white;
	padding: rem(8) rem(14);
	border: rem(0.5) solid rgba(0, 0, 0, 0.1);

	transition: all 250ms ease;

	span {
		font-size: rem(10);
		line-height: 148%;
		color: black;

		transition: all 250ms ease;
	}
}

.button-gray {
	border-radius: rem(1);
	background: rgba(160, 160, 160, 0.4);
	box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.04),
	0px 4px 25px rgba(0, 0, 0, 0.05),
	inset 0px 1px 2px rgba(254, 254, 254, 0.22);
	backdrop-filter: blur(4px);

	padding: rem(9.5) rem(50);

	display: inline-block;

	span {
		font-weight: 500;
		font-size: rem(12);
		line-height: 148%;
		color: white;
	}
}


.title-32 {
	font-size: rem(32);
	font-weight: 900;
	line-height: 126%;
}

.title-24 {
	font-size: rem(24);
	font-weight: 600;
}


.field-checked {
	position: relative;

	cursor: pointer;

	display: flex;
	flex-wrap: wrap;
	align-items: center;

	margin-bottom: rem(10);

	&:last-child {
		margin-bottom: 0;
	}

	input {
		display: none;
	}

	.check {
		position: relative;
	}

	.text {
		font-size: rem(12);
	}
}

.field-checked.type-square {

	input {
		&:checked {
			~ .check {
				border-color: var(--c-black-80);

				&::before {
					content: "\e903";
					font-family: 'icons';
					color: var(--c-red);
					font-size: rem(7);
				}
			}

			~ .text {
				color: var(--c-black-80);
			}
		}
	}

	.check {
		display: flex;
		align-items: center;
		justify-content: center;

		margin-right: rem(6);

		width: rem(15);
		height: rem(15);
		border-radius: rem(2);
		border: rem(1) solid var(--c-black-50);
	}

	.text {
		color: var(--c-black-50);
	}
}

.field-checked.type-circle {

	input {
		&:checked {
			~.check {
				background-color: black;
			}

			~.text {
				color: black;
			}
		}
	}

	.check {
		width: rem(4);
		height: rem(4);
		border-radius: 50%;
		background-color: var(--c-black-50);
		margin-right: rem(6);
	}

	.text {
		color: var(--c-black-50);
	}

}

.main-wrap {
	flex: 0 0 rem(894);
	max-width: rem(894);
}

.section-header {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(27);

	.title-24, .title-32 {
		margin-bottom: rem(18);
	}

	.flex {
		margin-bottom: rem(18);
		.title-24, .title-32 {
			margin-bottom: 0;
		}
		a {
			align-self: flex-end;
		}
	}

	a {
		color: var(--c-black-50);
		font-size: rem(12);
		font-weight: 500;

		&:hover {
			color: black;
		}
	}

	p {
		font-size: rem(16);
		color: var(--c-black-30);
	}
}

.field-set {
	position: relative;
	margin-bottom: rem(18);

	&:last-child {
		margin-bottom: 0;
	}
}

.field {
	border-radius: rem(5);
	height: rem(50);
	width: 100%;
	padding: 0 rem(20);

	transition: all 250ms ease;

	font-size: rem(12);
	font-weight: 500;

	border: rem(0.5) solid rgba(0, 0, 0, 0.2);

	&:focus {
		border-color: black;
	}

	@include placeholder {
		font-size: rem(12);
		font-weight: 500;
		color: black;
	}
}

textarea.field {
	padding: rem(14) rem(20);
	min-height: rem(110);
	max-height: rem(200);
	resize: vertical;
}

.field-attach {
	width: 100%;
	cursor: pointer;
	height: rem(50);
	border-radius: rem(5);
	border: rem(0.5) dashed rgba(0, 0, 0, 0.5);
	border-width: rem(1);

	padding: rem(18) rem(20);

	display: flex;
	flex-wrap: wrap;
	align-items: center;

	&:hover {
		span {
			text-decoration: none;
		}
	}

	input {
		display: none;
	}

	i {
		font-size: rem(12);

		margin-right: rem(10);
	}

	span {
		color: var(--c-blue);
		font-size: rem(10);
		font-weight: 500;
		text-decoration: underline;

		transition: all 250ms ease;
	}
}