.section-reviews {
	margin-bottom: rem(64);

	&__control {
		position: relative;
		margin-top: rem(18);

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		
		height: rem(44);

		.swiper-nav-wrap {
			position: absolute;
			bottom: 0;
			left: 0;
		}

		.swiper-nav-prev,
		.swiper-nav-next {
			box-shadow: none;
		}

		.swiper-pagination {
			position: relative;
		}
	}
}

.review_item {
	border-radius: rem(5);
	border: rem(0.5) solid rgba(0, 0, 0, 0.2);
	background-color: white;
	display: flex;
	flex-direction: column;

	height: rem(282);

	padding: rem(14) rem(20) rem(20) rem(23);

	transition: all 250ms ease;

	&:hover {
		box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.07),
		0px 4px 25px rgba(0, 0, 0, 0.1);
		
		.review_item__image {
			i {
				&::before {
					color: #FF7B7B;
				}
			}
		}

		.review_item__text {
			color: rgb(33, 33, 33);
		}

		.review_item__author {
			color: black;
		}


	}

	&__image {
		position: relative;
		margin-bottom: rem(16);

		img {
			max-width: rem(100);
			border-radius: rem(5);
			object-fit: cover;
		}

		i {
			font-size: rem(30);

			position: absolute;
			bottom: rem(-8);
			left: 0;

			&::before {
				color: #ECECEC;
			}
		}
	}

	&__text, &__author {
		transition: all 250ms ease;
	}

	&__text {
		font-size: rem(13);
		line-height: 162%;
		color: rgba(33,33,33,.5);
		max-width: rem(233);
		
	}

	&__author {
		margin-top: auto;
		font-size: rem(12);
		font-weight: 500;
		line-height: 130%;
		color: var(--c-black-60);
	}
}