:root {
	// --var: ....
	--default-font: 'Inter';

	--c-text: #212121;
	--c-text-60: rgba(33,33,33,.6);

	--c-blue: #0047FF;
	--c-red: #FF0000;
	--c-gray: #8D8D8D;
	--c-gray-field: #757575;
	--c-black-80: rgba(0, 0, 0, .8);
	--c-black-70: rgba(0,0,0,.7);
	--c-black-60: rgba(0,0,0,.6);
	--c-black-50: rgba(0, 0, 0, .5);
	--c-black-40: rgba(0, 0, 0, .4);
	--c-black-30: rgba(0, 0, 0, .3);
	--c-black-20: rgba(0, 0, 0, .2);
	--c-black-10: rgba(0, 0, 0, .1);
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behaior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

a, a span, input, button, button span, select, i, i::before {
	transition: all 250ms ease;
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

i {
	vertical-align: middle;
}

input, button {
	font-family: var(--default-font);
	font-size: rem(14);
}

b {
	font-weight: 500;
}

li {
	list-style: none;
}

body {
	// position: relative;
	font-size: rem(14);
	font-family: var(--default-font);
	min-width: 320px;
	// height: 100%;
	color: #000000;
	// overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%;

	margin-left: rem(-24);
	margin-right: rem(-24);
	padding-left: rem(24);
	padding-right: rem(24);
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}


.wrap-col-2 {
	display: flex;
	flex-wrap: wrap;
}